import React from 'react';
import { Select as MuiSelect, MenuItem } from '@mui/material';

const Select = ({ children, placeholder, ...rest }) => {
  return (
    <MuiSelect
      displayEmpty
      renderValue={(value) => {
        if (value !== '') {
          const display = React.Children.toArray(children).find((child) => child?.props?.value === value);
          if (display) return display.props.children;

          return (
            <MenuItem disabled sx={{ padding: 0 }}>
              {placeholder}
            </MenuItem>
          );
        } else {
          return (
            <MenuItem disabled sx={{ padding: 0 }}>
              {placeholder}
            </MenuItem>
          );
        }
      }}
      {...rest}
    >
      {children}
    </MuiSelect>
  );
};

export default Select;
