import React, { useState } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  TableContainer,
  Table,
  TableBody,
  TableHead,
  TableCell,
  TableRow,
  Paper,
  Pagination,
  InputAdornment,
  Select,
  Grid,
  Container,
  Link
} from '@mui/material';
import { Search, FilterAlt } from '@mui/icons-material';
import FormLabel from 'src/components/FormLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
// import Select from '@mui/material/Select';
import Chip from '@mui/material/Chip';
import AppSelect from 'src/components/Select';
import { NavLink } from 'react-router-dom';

const categories = [
  {
    value: 1,
    text: 'category 1'
  },
  {
    value: 2,
    text: 'category 2'
  },
  {
    value: 3,
    text: 'category 3'
  },
  {
    value: 4,
    text: 'category 4'
  }
];

const sortByOptions = [
  {
    value: 1,
    text: 'Last modified'
  },
  {
    value: 2,
    text: 'Name'
  },
  {
    value: 3,
    text: 'Due'
  }
];

const item = { name: 'Proposal Zero Six', sentTime: 'Nov 23, 2021 - 12:59', due: 'Nov 23, 2021 - 12:59', recipient: 'Alex benjamin', status: '2012' };

const ReceivedContractList = () => {
  const [category, setCategory] = useState();
  const [personName, setPersonName] = React.useState([]);
  const [sortBy, setSortBy] = useState();

  const handleChange = (event) => {
    const {
      target: { value }
    } = event;
    setPersonName(
      // On autofill we get a stringified value.
      typeof value === 'string' ? value.split(',') : value
    );
  };

  return (
    <Box>
      <Box mb={4} display="flex" justifyContent="space-between">
        <Typography fontWeight="bold">Received Contract</Typography>
      </Box>

      <Grid container spacing={2}>
        <Grid item xs={3}>
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Search"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Search />
                </InputAdornment>
              )
            }}
          />
        </Grid>
        <Grid item xs={3}>
          <Select
            placeholder="dsfdf"
            fullWidth
            multiple
            value={personName}
            onChange={handleChange}
            input={<OutlinedInput placeholder="Select categories" />}
            renderValue={(selected) => (
              <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                {selected.map((value) => (
                  <Chip key={value} label={value} />
                ))}
              </Box>
            )}
          >
            {categories.map((category) => (
              <MenuItem key={category.value} value={category.text}>
                {category.text}
              </MenuItem>
            ))}
          </Select>
        </Grid>
        <Grid item xs={3}>
          <Button variant="contained" endIcon={<FilterAlt />}>
            More filter
          </Button>
        </Grid>
        <Grid item xs={3}>
          <Box display="flex" alignItems="center">
            <Typography mr={1} sx={{ whiteSpace: 'nowrap' }}>
              Sort by
            </Typography>
            <AppSelect fullWidth placeholder="Select Categories" value={sortBy} onChange={(event) => setSortBy(event.target.value)}>
              {sortByOptions.map((option, key) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.text}
                </MenuItem>
              ))}
            </AppSelect>
          </Box>
        </Grid>
      </Grid>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Sent time</TableCell>
              <TableCell>Due</TableCell>
              <TableCell>Recipient</TableCell>
              <TableCell>Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {new Array(10).fill().map((n, index) => (
              <TableRow key={index}>
                <TableCell>
                  <Link component={NavLink} to="/slate/receive-contract/details">
                    {' '}
                    {item.name}
                  </Link>
                </TableCell>
                <TableCell>{item.sentTime}</TableCell>
                <TableCell>{item.due}</TableCell>
                <TableCell>{item.recipient}</TableCell>
                <TableCell>{item.status}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Box mt={3} display="flex" justifyContent="center">
        <Pagination count={50} />
      </Box>
    </Box>
  );
};

export default ReceivedContractList;
